import React from 'react';
import { graphql } from 'gatsby';

const ExamplePage = ({ data }) => {
  const photos = data.allPhoto.edges.map(edge => edge.node);
  console.log(photos)
  return (
    <div>
      {photos.map(photo => (
        <div key={photo.id}>
          <h2>{photo.title} - {photo.id} - {photo.ph_uuid}</h2>
          <img src={photo.localImage.childImageSharp.fluid.src} alt={photo.title} />
        </div>
      ))}
    </div>
  );
};

export default ExamplePage

export const query = graphql`
  query {
    allPhoto {
      edges {
        node {
          id
          ph_uuid
          title
          url
          localImage {
            childImageSharp {
              fluid {
                src
              }
            }
          }
        }
      }
    }
  }
`;